"use client";

import * as React from "react";
import { useSearchParams } from "next/navigation";
import { getAllCandiesAction } from "@/app/actions";
import { CandyInsights } from "@/components/candy-insights";
import { AddCandyFormWrapper } from "@/components/add-candy-form-wrapper";
import { CandyList } from "@/components/candy-list";
import { YearFilterBar } from "@/components/year-filter-bar";
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Separator } from "@/components/ui/separator";
import { Badge } from "@/components/ui/badge";
import type { Candy } from "@/lib/candy";

interface CandyDashboardProps {
  initialYear: number;
  currentYear: number;
  initialCandies: Candy[];
}

export function CandyDashboard({ 
  initialYear, 
  currentYear,
  initialCandies
}: CandyDashboardProps) {
  const [selectedYear, setSelectedYear] = React.useState(initialYear);
  const [candies, setCandies] = React.useState<Candy[]>(initialCandies);
  const [isLoading, setIsLoading] = React.useState(false);
  const searchParams = useSearchParams();

  // Track if this is the initial mount
  const isInitialMount = React.useRef(true);

  // Fetch data when year changes (but not on initial mount if we have initial data)
  React.useEffect(() => {
    // On initial mount, skip if we have initial data for the selected year
    if (isInitialMount.current) {
      isInitialMount.current = false;
      // We already have initial data from server, don't refetch
      return;
    }
    
    // Year changed, fetch new data
    const fetchData = async () => {
      setIsLoading(true);
      try {
        const data = await getAllCandiesAction(selectedYear);
        setCandies(data);
      } catch (error) {
        console.error("Error fetching candies:", error);
        setCandies([]);
      } finally {
        setIsLoading(false);
      }
    };
    fetchData();
  }, [selectedYear]); // Re-fetch when year changes

  // Update URL silently (without navigation)
  const handleYearChange = React.useCallback((year: number) => {
    setSelectedYear(year);
    
    // Update URL silently for bookmarking
    const params = new URLSearchParams(searchParams.toString());
    if (year === currentYear) {
      params.delete("year");
    } else {
      params.set("year", year.toString());
    }
    
    const newUrl = params.toString() ? `/?${params.toString()}` : '/';
    window.history.replaceState({}, '', newUrl);
  }, [currentYear, searchParams]);

  // Refetch when candy is added (only for current year)
  const handleCandyAdded = React.useCallback(() => {
    if (selectedYear === currentYear) {
      getAllCandiesAction(currentYear).then(setCandies).catch(console.error);
    }
  }, [selectedYear, currentYear]);

  const isCurrentYear = selectedYear === currentYear;

  return (
    <div className="space-y-8">
      {/* Global Filter Bar */}
      <YearFilterBar
        selectedYear={selectedYear}
        currentYear={currentYear}
        onYearChange={handleYearChange}
        isLoading={isLoading}
      />

      {/* Insights */}
      <Card>
        <CardHeader>
          <CardTitle className="text-xl flex items-center gap-2">
            Insights
            {selectedYear !== currentYear && (
              <Badge variant="secondary">{selectedYear}</Badge>
            )}
          </CardTitle>
        </CardHeader>
        <CardContent>
          <CandyInsights candies={candies} />
        </CardContent>
      </Card>

      <Separator />

      {/* Add Candy */}
      <Card>
        <CardHeader>
          <CardTitle className="text-xl">Add Candy</CardTitle>
        </CardHeader>
        <CardContent>
          <AddCandyFormWrapper 
            disabled={!isCurrentYear}
            disabledMessage={`You can only add candy for the current year (${currentYear}). Select ${currentYear} to add candy.`}
            onSuccess={handleCandyAdded}
          />
        </CardContent>
      </Card>

      <Separator />

      {/* Your Candy Collection */}
      <Card>
        <CardHeader>
          <CardTitle className="text-xl">Your Candy Collection</CardTitle>
        </CardHeader>
        <CardContent>
          <CandyList 
            initialCandies={candies}
            selectedYear={selectedYear}
            currentYear={currentYear}
          />
        </CardContent>
      </Card>
    </div>
  );
}

